export const alphaToNumber = (q) => {
    const list = 'abcdefghijklmnopqrstuvwxyz';
    return q.split('')
    .map(char => {
        const position = list.indexOf(char.toLowerCase())
        if(position > -1) {
            return position + 1;
        } else {
            if(!isNaN(char)) {
                return char;
            } else {
                return " ";
            }   
        }
    })
    .join('')
}


export const numberToStar = (num) => {
    return Object.keys(combo).reduce((prev, k) => {
        const check = `${num}`;
        
        if(prev) {
            return prev;
        }
        if(combo[k].includes(check)) {

            return {
                type: k.substr(0, k.length - 1),
                level: k.substr(-1),
                num: check
            }
        } else {
            return '';
        }
    }, '')
}

