import * as React from "react"
import { useState } from "react"
import { HeadFC, PageProps, graphql, useStaticQuery } from "gatsby"
import { breakNumber, numberToStar, numberMeaning, stars, combo, alphaToNumber } from '../helpers/NumberEnergy'
import Ad from '../components/ad'
import { Trans, useTranslation } from 'gatsby-plugin-react-i18next';
import Layout from '../components/layout/layout'
import Parse from '../vendor/Parse'

const IndexPage: React.FC = () => {

  const { t } = useTranslation();
  const [query, setQuery] = useState('')
  const [numberChunk, setNumberChunk] = useState([])
  const [moreClause, setMoreClause] = useState([])

  const analyseNumber = (query) => {
    const result = []
    const chunk = breakNumber(query);
    const lastNumberPair = chunk[chunk.length - 1];

    const starRepresentation = chunk.map(e => numberToStar(e).type)


    const numOfStar = stars.map(star => {
      return {
        star,
        count: starRepresentation.filter(_star => _star === star).length
      }
    })

    if (query.includes('91') || query.includes('87')) {
      result.push(t('NumNoLadies'))
    }

    if (query.includes('0')) {
      result.push(t("avoidZero"))
    }

    if (['wugui', 'liusha', 'huohai', 'jueming'].includes(numberToStar(chunk[chunk.length - 1]).type)) {
      result.push(`Avoid ${lastNumberPair} at the end.`)
    }

    numOfStar.forEach(e => {
      if (e.count > 2) {
        result.push(`${e.count} ${t(e.star)} star in the number. Too many of a certain star may not be a good thing.`)
      }
    })




    return result;
  }

  const handleQuery = () => {
    const chunk = breakNumber(alphaToNumber(query))
    setNumberChunk(chunk)
    setMoreClause(analyseNumber(query))
  }

  React.useEffect(() => {
    const chunk = breakNumber(alphaToNumber(query))
    setNumberChunk(chunk)
    setMoreClause(analyseNumber(query))
  }, [query])

  const isGoodEnergy = (star) => ['tianyi', 'yannian', 'shengqi'].includes(star.trim())

  return (
    <Layout>
    <main className="page-main">
    <div className="section-banner">
      <div className="section-banner__bg" style={{ backgroundImage: "url(assets/img/bg-banner.jpg)" }} >
        <div className="uk-container uk-text-center">
          <div className="section-banner__content">
            <div className="section-banner__title"> { t("heroText") }</div>
            <div className="section-banner__text"> { t("harnessWisdom") }</div>
            <div className="section-banner__form">
              <form action="#!">
                <div className="form-search">
                  <div className="form-search__box">
                    <input placeholder={t('inputPlaceholder')} autoFocus={true} type="text" value={query} onChange={(e) => setQuery(e.target.value)} />
                    <a className="uk-button uk-button-danger" href="#analysis" type="submit" > { t("Check")} </a></div>
                    
                </div>
              </form>
            </div>
            <div className="section-banner__bottom">
              <a href="#disclaimer"> {t('disclaimer')}</a> 
              <Ad />
            </div>
          </div>
        </div>
      </div>
    </div>
    {
      query && !!numberChunk.length && <div className="section-info" id="analysis">
        <div className="uk-section-large uk-container uk-container-large">
          <div className="uk-grid uk-child-width@m" data-uk-grid>
            <div className="uk-width-3-4@m ">
              <div className="section-content">
                <h2> {t("numberEnergy")} {query} ({ alphaToNumber(query) }) </h2>
                <ul className="info-list">

                  {
                    stars.map((star, i) => {
                      const starChunk = numberChunk.filter(num => numberToStar(num).type === star)
                        .map(numberToStar)
                      if (starChunk.length === 0) {
                        return null
                      }
                      return <li key={i}>
                        <div className="info-list-item">
                          <div className="info-list-item__desc">
                            <div className="info-list-item__text">
                              <h5>{starChunk

                                .map(star => { return `${t(star.type)} (${star.num}) Star  Lvl ${star.level}` })
                                .join(' => ')
                              }
                              </h5>
                              <div>
                                {t(`${star}_representation`)}
                              </div>
                            </div>
                          </div>
                        </div>
                      </li>
                    })
                  }
                </ul>
              </div>
            </div>


          </div>
        </div>
      </div>
    }
    <div className="section-steps">
      <div className="uk-section-large uk-container">
        <div className="section-title uk-text-center"><span>{t('enhancementText')}</span>
          <h3 className="uk-h3"> {t("recommendation")}</h3>

          <div className="">

            {
              moreClause.length === 0 && t('noRecommendation')
            }
            {
              moreClause && moreClause.map((c, i) => <div key={i}>
                {i + 1}）{c}
                <br />
                <br />
              </div>)
            }
          </div>
        </div>
      </div>
    </div>

    <div className="section-info">
      <div className="uk-section-large uk-container">
        <div className="section-title uk-text-center"><span>{ t('starAttributes') } </span>
          <h3 className="uk-h3"> { t('starChart') } </h3>
          <div className="star-chart2">
            <div className='div1'>
              
            </div>
            <div className='div2'>
            { t("band1") }
            
            </div>
            <div className='div3'>
            { t("band2") }
            
            </div>
            <div className='div4'>
            { t("band3") }
            
            </div>
            <div className='div5'>
            { t("band4") }
            </div>
          </div>
          <div className="star-chart">
            {
              stars.map((s, i) => <>
                <div className={`div${i * 5 + 1} ${isGoodEnergy(s) && 'uk-color-red'}`}> {t(s)} </div>
                <div className={`div${i * 5 + 2} ${isGoodEnergy(s) && 'uk-color-red'}`}> {combo[`${s}1`].join('/')} </div>
                <div className={`div${i * 5 + 3} ${isGoodEnergy(s) && 'uk-color-red'}`}> {combo[`${s}2`].join('/')}</div>
                <div className={`div${i * 5 + 4} ${isGoodEnergy(s) && 'uk-color-red'}`}> {combo[`${s}3`].join('/')}</div>
                <div className={`div${i * 5 + 5} ${isGoodEnergy(s) && 'uk-color-red'}`}> {combo[`${s}4`].join('/')}</div>

              </>)
            }
          </div>
        </div>
      </div>
    </div>
  </main>
  </Layout>
  )
}

export default IndexPage

export const Head: HeadFC = () => <title>Is Your Number Lucky?</title>


export const query = graphql`
  query($language: String!) {
    locales: allLocale(filter: {language: {eq: $language}}) {
      edges {
        node {
          ns
          data
          language
        }
      }
    }
  }
`;
